<template>
  <Header />
  <div id="contact-page" class="container">
    <div class="bg">
      <div class="row" style="margin-bottom: 50px">
        <div class="col-sm-12">
          <h2 class="title text-center">
            <router-link to="/Add_new_product">إضافة منتج جديد</router-link>
          </h2>
          <!--<div id="gmap" class="contact-map">
					</div>-->
        </div>
      </div>
      <div class="row">
        <div class="col-sm-8">
          <div class="contact-form">
            <div class="status alert alert-success" style="display: none"></div>
            <form
              id="main-contact-form"
              class="contact-form row"
              name="contact-form"
              @submit.prevent="addnots"
            >
              <div class="form-group col-md-6">
                <input
                  type="text"
                  name="name"
                  v-model="name"
                  class="form-control"
                  required="required"
                  placeholder="اسم المنتج"
                />
              </div>
              <div class="form-group col-md-6">
                <input
                  type="text"
                  name="text"
                  v-model="email"
                  class="form-control"
                  required="required"
                  placeholder="السعر"
                />
              </div>
              <div class="form-group col-md-6">
                <select
                  name="subject"
                  v-model="subject"
                  class="form-control"
                  value=""
                >
                  <option disabled="disabled" selected="selected">
                    المنتج
                  </option>
                  <option value="اكسسوريز">اكسسوريز</option>
                  <option value="عنايه">عنايه</option>
                  <option value="مكياج">مكياج</option>
                  <option value="شنط">شنط</option>
                  <option value="عطور">عطور</option>
                  <option value="اجهزه">اجهزه</option>
                  <option value="ملابس نساء">ملابس نساء</option>
                  <option value="رجال">رجال</option>
                </select>
              </div>
              <div class="form-group col-md-6">
                <input
                  type="file"
                  name="image"
                  class="form-control"
                  placeholder="اختر صورة المنتج"
                />
              </div>
              <div class="form-group col-md-12">
                <textarea
                  name="message"
                  id="message"
                  v-model="message"
                  required="required"
                  class="form-control"
                  rows="8"
                  placeholder="وصف المنتج أو نبذة عنه"
                ></textarea>
              </div>
              <div class="form-group col-md-12">
                <input
                  type="submit"
                  name="submit"
                  class="btn btn-primary pull-right"
                  value="اضافة"
                  @click="addnot"
                />
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!--/#contact-page-->
  <Footer />
</template>
<script>
import Footer from "../components/Footer.vue";
import Header from "../components/Header.vue";
export default {
  components: { Header, Footer },
  name: "Add_new_product",
  data: function () {
    return {
      A: {
        name: "",
        email: "",
        subject: "",
        message: "",
        date: new Date(Date.now()).toLocaleString(),
      },
    };
  },

  methods: {
    addnots: function () {
      var addnot = {
        namea: this.name,
        emaill: this.email,
        subjectt: this.subject,
        date: new Date(Date.now()).toLocaleString(),
        messagee: this.message,
      };
      this.$store.state.skils.push(addnot);

      this.$router.push("/Control_panel");
    },

    // addnew: function () {

    //      {
    //   name:this.$store.state.name,
    //   email:this.$store.state.email,
    //   subject:this.$store.state.subject,
    //   message:this.$store.state.message,
    //   date:new Date(Date.now()).toLocaleString(),
    //   };
    // },
  },
};
</script>
