<template>
  <div>
    <Header />
    <section id="cart_items">
      <div class="container">
        <div class="breadcrumbs">
          <ol class="breadcrumb">
            <li><router-link to="/Control_panel">الرئيسية</router-link></li>
            <li class="active">لوحة التحكم</li>
          </ol>
        </div>

        <h4 style="margin-bottom: 3rem">
          <router-link to="/Add_new_product">إضافة منتج جديد</router-link>
        </h4>

        <div class="table-responsive cart_info">
          <table class="table table-condensed">
            <thead>
              <tr class="cart_menu">
                <td class="image">المنتج</td>
                <td class="description">اسم المنتج</td>
                <td class="description">الصنف</td>
                <td class="price">السعر</td>
                <td class="price">تاريخ الاضافة</td>
                <td class="total">العمليات</td>
                <td></td>
              </tr>
            </thead>
            <tbody
              v-for="(item, index) in this.$store.state.skils"
              :key="index"
            >
              <tr>
                <td class="cart_product">
                  <router-link to=""
                    ><img :src="imgs" :width="widget" alt=""
                  /></router-link>
                </td>
                <td class="cart_description">
                  <h4>
                    <a href="">{{ item.namea }}</a>
                  </h4>
                </td>
                <td class="cart_description">
                  <h4>
                    <router-link to="/Control_panel">
                      {{ item.subjectt }}
                    </router-link>
                  </h4>
                </td>
                <td class="cart_price">
                  <p>${{ item.emaill }}</p>
                </td>
                <td class="cart_description">
                  <p>{{ item.date }}</p>
                </td>
                <td class="cart_delete">
                  <router-link
                    class="cart_quantity_delete"
                    @click="removeTask(index)"
                    to=""
                  >
                    <i class="fa fa-times"></i>
                  </router-link>
                  <router-link class="cart_quantity_delete" to="">
                    <i class="fa fa-pencil-square-o" @click="update(index)"></i>
                  </router-link>
                  <router-link class="cart_quantity_delete" to="">
                    <i class="fa fa-info-circle" @click="show(index)"></i>
                  </router-link>
                </td>
              </tr>
            </tbody>
            <!-- <Contr  v-for="(item,index) in this.$store.state.skils" :key="index" /> -->
          </table>
        </div>
      </div>
    </section>
    <!--/#cart_items-->
    <Footer />
  </div>
</template>
<script>
// import Contr from "../components/Contr.vue";
import Footer from "../components/Footer.vue";
import Header from "../components/Header.vue";

export default {
  components: { Footer, Header },
  name: "Control_panel",
  data: function () {
    return {
      imgs: "/images/products/8.jpeg",
      widget: 70,
    };
  },
  methods: {
    removeTask: function (index) {
      this.$store.state.skils.splice(index, 1);
    },
    
    show: function (index) {
      this.$store.state.skils.reverse(index, 1);
      this.$router.push("/Product_details");
    },

    update: function (index) {
      this.$store.state.skils.set(index, 1);
      this.$router.push("/update");
    },
  },
};
</script>