<script>
import Footer from "../components/Footer.vue";


export default {
  components: { Footer },
  name: "Shop",
  data: function () {
    return {
      imgs: "/images/products/8.jpeg",
      search: "",
      datsa: this.$store.state.skils,
    };
  },
  computed: {
    skils() {
      return this.$store.state.skils;
    },
    filter() {
      return this.skils.filter(({ emaill, namea }) =>
        [emaill, namea].some((val) =>
          val.toLowerCase().includes(this.search)
        )
      );
    },
  },
};
