import { createRouter, createWebHistory } from "vue-router";
import About from "../views/About.vue";
import Index from "../views/Index.vue";
import Error from "../views/Error.vue";
import Add_new_product from "../views/Add_new_product.vue";
import Control_panel from "../views/Control_panel.vue";
import Login from "../views/Login.vue";
import Product_details from "../views/Product_details.vue";
import Register from "../views/Register.vue";
import Shop from "../views/Shop.vue";
import update from "../views/update.vue";

const routes = [
  {
    path: "/",
    name: "Index",
    component: Index,
  },
  {
    path: "/about",
    name: "About",
    component: About,
  },
  {
    path: "/Error",
    name: "Error",
    component: Error,
  },
  {
    path: "/Add_new_product",
    name: "Add_new_product",
    component: Add_new_product,
  },
  {
    path: "/Control_panel",
    name: "Control_panel",
    component: Control_panel,
  },
  {
    path: "/Login",
    name: "Login",
    component: Login,
  },
  {
    path: "/Product_details",
    name: "Product_details",
    component: Product_details,
  },
  {
    path: "/Register",
    name: "Register",
    component: Register,
  },
  {
    path: "/Shop",
    name: "Shop",
    component: Shop,
  },
  {
    path: "/update",
    name: "update",
    component: update,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
